<script>
import TabBar from '@/components/NavBar/TabBar.vue'
export default {
    components:{
       TabBar 
    },
    data() {
        return {
            images: [
                'https://img.yzcdn.cn/vant/apple-1.jpg',
                'https://img.yzcdn.cn/vant/apple-2.jpg'
            ],
            isLoading: false,
            num: 8
        }
    },
    mounted() {
        setTimeout(() => {
                this.num = 8
            }, 1000);
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.count++;
            }, 1000);
        }
    }
    
}
</script>

