import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate';
Vue.use(Vuex)

/* 要设置的全局访问的state对象 */
const state = {
    backgroundImage: 'BC008',
    title: 'a-biu'
};


/* 实时监听state值的变化(最新状态) */
const getters = {
    /* 方法名随意,主要是用来承载变化的changableNum的值 */
    getBackgroundImage(state) {
        return state.backgroundImage
    },
    getTitle(state) {
        return state.title
    }

};
/* 自定义同步改变state初始值的方法 */
const mutations = {
    /* 同上，这里面的参数除了state之外还传了需要增加的值sum */
    newBackgroundImage(state, sum) {
        state.backgroundImage = sum;
    },
    newTitle(state, text) {
        state.title = text;
    }
};
/* 自定义异步触发mutations里函数的方法， */
const actions = {
    /* 同上注释，num为要变化的形参 */
    getNewBackgroundImage(context, num) {
        context.commit('newBackgroundImage', num)
    },
    getNewTitle(context, text) {
        context.commit('newTitle', text)
    }
};

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
    plugins: [createPersistedState({
        // storage: window.sessionStorage,
        // reducer(val) {
        //     return {
        //         // 只储存state中的assessmentData
        //         changableNum: val.changableNum
        //     }
        // }
    })]
})