<!--
 * @Description: tab | herder | 带侧边栏无返回按钮
 * @Date: 2020-01-16 13:47:56
 * @LastEditors  : Astronautics across the sea of stars
 * @LastEditTime : 2020-01-19 17:00:52

  ----------------------------------------------------------
  | 参数	      | 说明	           |  类型	     |   默认值  |
  ----------------------------------------------------------
  | title	      | 标题	           |  string	  |   ''      |
  ----------------------------------------------------------
  | left-text	  | 左侧文案	       |  string	   |  ''      |
  ----------------------------------------------------------
  | right-text	| 右侧文案	       |  string	   |  ''      |
  ----------------------------------------------------------
  | left-arrow	| 是否显示左侧箭头	|  boolean	  |  false   | 
  ----------------------------------------------------------
  | fixed	      | 是否固定在顶部	  |  boolean	  |  false   |
  ----------------------------------------------------------
  | border	    | 是否显示下边框	  |  boolean	  |  true    |
  ----------------------------------------------------------
  | z-index	    | 元素             |  z-index	   |  number  |
  ----------------------------------------------------------
  
 -->
<template>
  <div class="bar_">
    <van-nav-bar
      :title="getTitle"
      
      right-text="|||"
      :fixed="true"
      @click-left="onClickLeft"
      @click-right="onClickRight"
    >
    <van-icon name="wap-nav" slot="right" size="22" />
    </van-nav-bar>
    <van-popup
      v-model="show"
      position="right"
      :style="{ width: '60%', height: '100%' }"
    >
    <p>
    <van-row>
      <van-col span="6">
      <van-image
      round
      width="30px"
      height="30px"
      src="https://img.yzcdn.cn/vant/cat.jpeg"
    />
      </van-col>
      <van-col span="18">
      <p style="margin: 5px;float: left;font-weight: 600;">joe888</p> 
      
      </van-col>
    </van-row>
    </p>
    <p>
      <van-button type="primary" size="mini" to="logIn">登录</van-button>
      <van-button type="primary" size="mini" to="logOut">注册</van-button>
    </p>
    <van-cell title="余额" label="200356.51" clickable icon="location-o" />
    <van-cell title="个人中心" clickable icon="location-o" />
    <van-cell title="我的钱包" clickable icon="location-o" />
    <van-cell title="快速提现" clickable icon="location-o" />
    <van-cell title="账户流水" clickable icon="location-o" />
    <van-cell title="我的订单" clickable icon="location-o" />
    <van-cell title="在线客服" clickable icon="location-o" />
    </van-popup>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false
    }
  },
  computed: {
     getTitle() {
        return this.$store.getters.getTitle;
    }
  },
  methods: {
    onClickLeft() {
      this.$toast("按钮 > 返回");
    },
    onClickRight() {
      this.$toast("按钮 > 侧边");
      this.showPopup();
    },
    showPopup() {
      this.show = true;
    }
  }
};
</script>

<style lang="scss">
.bar_{
  .user_name{
    
  }
  .van-cell__title {
    text-align: left;
  }
  .van-cell__label{
    font-weight: 900;
    color: #ee0a24;
  }
}
</style>
